window.GFPPCPFormEditor = null;

(function ($) {
    window.GFPPCPFormEditor = function () {
        const self = this;

        self.init = function () {
            self.hooks();

            self.bindFieldAdded();
            self.bindLoadFieldSettings();
            self.bindSetPaymentMethod();
        };

        self.hooks = function () {
            gform.addAction('gform_post_load_field_settings', function ([field, form]) {
                if (field['type'] === 'paypal') {
                    // Render the Smart Payment Buttons.
                    self.renderSmartPaymentButtons(field);
                    self.bindRerenderSmartPaymentButtons();
                    $('.sub_labels_setting, .input_placeholders_setting').addClass('paypal');

                    // Hide #field_settings when the api is not initialized.
                    // This is called right after the settings are shown. So that makes it feel like there's no settings.
                    if (gform_ppcp_form_editor_strings.initialize_api === '') {
                        HideSettings('field_settings');
                    }

                    $('#field_paypal_default_payment_method').val(field['defaultPaymentMethod']).on('change', function () {
                        const newMethod = $(this).val();
                        $('.gform_ppcp_payment_method select').val(newMethod);
                        // Update the default payment method in the global form variable.
                        SetFieldProperty('defaultPaymentMethod', newMethod);
                    });

                    $('.input_placeholders_setting').on('input propertychange', '.input_placeholder', function () {
                        const inputId = $(this).closest('.input_placeholder_row').data('input_id');

                        if ($(this).attr('id').indexOf('.5') === -1) {
                            $('#input_' + inputId.toString().replace('.', '_')).text(this.value);
                        }
                    });

                    // Remove JCB card option.
                    $('.credit_card_setting ul li').each(function (i, elem) {
                        if ($(elem).children('input').val() === 'jcb') {
                            $(elem).hide();
                        }
                    });
                } else {
                    $('.sub_labels_setting, .input_placeholders_setting').removeClass('paypal');
                }
            });

            gform.addFilter('gform_form_editor_can_field_be_added', function (result, type) {
                if (type === 'paypal') {
                    if (GetFieldsByType(['paypal']).length > 0) {
                        alert(gform_ppcp_form_editor_strings.only_one_paypal_field);

                        result = false;
                    }
                }

                return result;
            });
        };

        self.bindFieldAdded = function () {
            $(document).bind('gform_field_added', function (event, form, field) {
                if (field.type === 'paypal') {
                    // Render the Smart Payment Buttons.
                    self.renderSmartPaymentButtons(field);
                }
            });
        };

        self.renderSmartPaymentButtons = function (field) {
            if ($('#gform_ppcp_smart_payment_buttons').length && typeof paypal !== 'undefined') {
                if (typeof field === "undefined") {
                    field = GetFieldsByType(['paypal'])[0];
                }

                $('#gform_ppcp_smart_payment_buttons').addClass(field.buttonsSize);

                paypal.Buttons({
                    style: {
                        layout: field.buttonsLayout,
                        color: field.buttonsColor,
                        shape: field.buttonsShape,
                    }
                }).render('#gform_ppcp_smart_payment_buttons');
            }
        };

        self.bindRerenderSmartPaymentButtons = function () {
            $('#smart_payment_buttons_container select').on('change', function () {
                const button = $('#gform_ppcp_smart_payment_buttons');
                const setting = $(this).attr('id');
                const value = $(this).val();

                button.html('');

                if (setting === 'buttonsSize') {
                    button.prop('class', '').addClass(value);
                }

                SetFieldProperty(setting, value);
                self.renderSmartPaymentButtons();
            });
        };

        self.bindLoadFieldSettings = function () {
            $(document).bind('gform_load_field_settings', function (event, field, form) {
                if (field['type'] === 'paypal') {
                    // If the field was previously set with an account that supports custom card fields,
                    // but now switch to another account doesn't, we dynamically update the methods here.
                    if (gform_ppcp_form_editor_strings.is_custom_card_fields_supported === '') {
                        field.methods = ['PayPal Checkout'];
                        SetFieldProperty('methods', field.methods);
                    }

                    for (let i in field.methods) {
                        if (field.methods[i] === 'Credit Card') {
                            $('#paypal_payment_custom_card_fields').prop('checked', true);
                        } else {
                            $('#paypal_payment_smart_payment_buttons').prop('checked', true);
                        }
                    }

                    for (let i in field.creditCards) {
                        if (!field.creditCards.hasOwnProperty(i))
                            continue;

                        $('#field_credit_card_' + field.creditCards[i]).prop('checked', true);
                    }
                    // Add the toggle to the cardholder name sub-label.
                    var imagesUrl = gform_ppcp_form_editor_strings.imgurl,
                        input = field['inputs'][4],
                        isHidden = typeof input.isHidden !== 'undefined' && input.isHidden ? true : false,
                        title = isHidden ? gform_ppcp_form_editor_strings.inactive : gform_ppcp_form_editor_strings.active,
                        img = isHidden ? 'active0.png' : 'active1.png';

                    $('.input_placeholders tr:eq(4)').toggle(!isHidden);

                    $('.sub_labels_setting .field_custom_inputs_ui tr:eq(0)').prepend('<td><strong>' + gform_ppcp_form_editor_strings.show + '</strong></td>');
                    $('.sub_labels_setting .field_custom_inputs_ui tr:eq(1), .sub_labels_setting .field_custom_inputs_ui tr:eq(2), .sub_labels_setting .field_custom_inputs_ui tr:eq(3), .sub_labels_setting .field_custom_inputs_ui tr:eq(5)').prepend('<td></td>');
                    $('.sub_labels_setting .field_custom_inputs_ui tr:eq(4)').prepend('<td><img data-input_id=\'' + field['id'] + '.5\' alt=\'' + title + '\' class=\'input_active_icon cardholder_name\' src=\'' + imagesUrl + img + '\'/></td>');

                    $('.sub_labels_setting').on('click keypress', '.input_active_icon.cardholder_name', function () {
                        const isHidden = this.src.indexOf('active0.png') >= 0;
                        $('#input_' + field['id'] + '_5_label').toggle(!isHidden);
                        $('.sub_labels_setting .field_custom_inputs_ui tr:eq(4) td:eq(2) input').prop('disabled', isHidden);
                        $('.input_placeholders tr:eq(4)').toggle(!isHidden);
                    });

                    // Set up the Smart Payment Button styles.
                    const buttonsSettings = ['buttonsLayout', 'buttonsSize', 'buttonsShape', 'buttonsColor'];
                    buttonsSettings.forEach(function (e) {
                        $('#' + e).val(field[e]);
                    });

                    // Toggle setting sections by supported payment methods.
                    if (field.methods.length === 1) {
                        const method = (field.methods[0] === 'Credit Card') ? 'PayPal Checkout' : 'Credit Card';
                        self.hideSettingSections(method);
                    }
                } else {
                    // Restore JCB card option.
                    $('.credit_card_setting ul li:hidden').show();
                }
            });
        };

        self.hideSettingSections = function (method) {
            $('.paypal_default_payment_method').fadeOut();

            if (method === 'Credit Card') {
                $('.smart_payment_buttons_note').fadeIn();

                $('.sub_labels_setting, .sub_label_placement_setting, .credit_card_setting, .credit_card_style_setting, .input_placeholders_setting, .label_placement_setting, .error_message_setting, .css_class_setting').fadeOut();
            } else {
                $('.smart_payment_buttons_settings').fadeOut();
            }
        };

        self.SetPaymentMethods = function (e, elem) {
            const methods = GetSelectedField()['methods'] ? GetSelectedField()['methods'] : new Array();
            const value = $(elem).val();

            if ($(elem).is(':checked')) {
                if ($.inArray(value, methods) === -1) {
                    if (value === 'Credit Card') {
                        $('.smart_payment_buttons_note').fadeOut();

                        $('.ginput_container_custom_card_fields').fadeIn();
                        $('.sub_labels_setting, .sub_label_placement_setting, .credit_card_setting, .credit_card_style_setting, .input_placeholders_setting, .label_placement_setting, .error_message_setting, .css_class_setting').fadeIn();
                    } else {
                        $('.smart_payment_buttons_settings').fadeIn();
                    }

                    $('.gform_ppcp_payment_method').fadeIn();
                    methods[methods.length] = value;

                    if (methods.length > 1) {
                        $('.paypal_default_payment_method').fadeIn();
                    }
                }
            } else {
                if (methods.length === 1) {
                    alert(gform_ppcp_form_editor_strings.must_have_method);

                    e.preventDefault();

                    return false;
                }

                const index = $.inArray(value, methods);
                if (index !== -1) {
                    self.hideSettingSections(value);

                    if (value === 'Credit Card') {
                        $('.ginput_container_custom_card_fields').fadeOut();
                    }

                    $('.gform_ppcp_payment_method').fadeOut();
                    methods.splice(index, 1);
                }
            }

            SetFieldProperty('methods', methods);
        };

        self.bindSetPaymentMethod = function () {
            $('.supported_payment_methods input:checkbox').on('click keypress', function (e) {
                self.SetPaymentMethods(e, this);
            });
        };

        self.init();
    };

    $(document).ready(GFPPCPFormEditor);
})(jQuery);